<template>
  <div class="component-main" ref="mainRef">
    <div class="inner">
      <h1 class="font-parisienne color-point">Publessing</h1>
      <br />
      <h2 class="font-parisienne color-point">
        <b>Draw the UI with commands</b>
      </h2>
      <h3 class="font-coding">
        <template v-if="store.state.account.login">
          <template v-if="store.state.site.lang === 'ko'">
            <b>create startup</b> 을 입력하여 페이지 생성 후<br />
            <b>open startup</b> 을 입력하여 페이지를 오픈해주세요
          </template>
          <template v-else>
            Enter "<b>create startup</b>" <br />
            then "<b>open startup</b>"
          </template>
        </template>
        <template v-else>
          <template v-if="store.state.site.lang === 'ko'">
            <div>아래의 <b>콘솔 열기</b> 버튼 클릭 후</div>
            <b>join</b> 혹은 <b>login</b> 을 입력해주세요
          </template>
          <template v-else>
            Click <b>OPEN CONSOLE</b> button below<br />
            then enter <b>join</b> or <b>login</b>
          </template>
        </template>
      </h3>
      <button class="btn btn-point" @click="callback('toggle-console')">
        <template v-if="store.state.site.lang === 'ko'">
          <span>콘솔 </span>
          <span v-if="visible">닫기</span>
          <span v-else>열기</span>
        </template>
        <template v-else>
          <span v-if="visible">CLOSE </span>
          <span v-else>OPEN </span>
          <span>CONSOLE</span>
        </template>
      </button>
      <router-link :to="`/${store.state.site.lang}/intro`" class="btn btn-point-line">{{ store.state.site.lang === "ko" ? "도구 소개" : "VIEW MORE" }}</router-link>
    </div>
  </div>
</template>
<script>
import { ref } from "@vue/reactivity";
import { useStore } from "vuex";
import { onMounted } from "@vue/runtime-core";

export default {
  props: {
    visible: Boolean,
    callback: Function,
    load: Function,
  },
  setup() {
    const store = useStore();
    const mainRef = ref(null);

    const setHeight = () => {
      mainRef.value.style.height = window.innerHeight - 60 + "px";
    };

    onMounted(setHeight);

    window.onresize = () => {
      setHeight();
    };

    return {
      store,
      mainRef,
    };
  },
};
</script>

<style lang="scss" scoped>
.component-main {
  position: relative;
  text-align: center;
  display: table;
  width: 100%;
  min-height: 550px;
  max-height: 1080px;

  > .inner {
    display: table-cell;
    vertical-align: middle;

    > h1 {
      font-size: 70px;
      font-weight: bold;
      display: inline-block;
      position: relative;
      margin-bottom: 25px;
    }

    > h2 {
      font-size: 24px;
      margin-bottom: 50px;
    }

    > h3 {
      font-size: 25px;
      line-height: 45px;
      margin-bottom: 40px;
    }

    > .btn {
      margin: 10px;
      height: 55px;
      line-height: 55px;
      font-size: 16px;
      padding: 0;
      width: 170px;
      box-shadow: none;
      outline: 0;
      border-radius: 0;
      font-weight: normal;
    }
  }
}
</style>
